.App {
  text-align: center;
}

header {
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  flex-wrap: wrap;
}

.logo{
  width: 40px;
  height: 40px;
  padding-right: 20px;
}

.eth{
  width: 20px;
  height: 20px;
  padding-right: 10px;

}

.leftH {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 10px 0;
}

.rightH {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 20px;
}

.headerItem {
  padding: 10px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  font-weight: 500;
  transition: 0.3s;
  display: flex;
  align-items: center;
}

.headerItem:hover {
  background-color: rgb(34, 42, 58);
  cursor: pointer;
  color: #E8FFCE;
}

.connectButton {
  background-color: #243056; 
  padding: 10px;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 100px;
  color: #E8FFCE;
  font-weight: bold;
  transition: 0.3s;
}

.connectButton:hover {
  cursor: pointer;
  color: #E8FFCE;
  background-color: #364881;
}

.mainWindow {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.link {
  text-decoration: none;
  color: #1f2639;
}

.tradeBox {
  width: 400px;
  background-color: #0E111B;
  border: 2px solid #21273a;
  min-height: 300px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 30px;
  padding-right: 30px;
  color: #E8FFCE;
}


.tradeBoxHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 98%;
  color: #E8FFCE;
}

.cog {
  color: #E8FFCE;
  transition: 0.3s;
}

.cog:hover {
  color: white;
  rotate: 90deg;
  cursor: pointer;
}

.switchButton {
  background-color: #3a4157;
  width: 25px;
  height: 25px;
  align-items: center;
  justify-content: center;
  display: flex;
  border-radius: 8px;
  position: absolute;
  top: 86px;
  left: 180px;
  color: #5F6783;
  border: 3px solid #0E111B;
  font-size: 12px;
  transition: 0.3s
}

.dollarAmount {
  position: absolute;
  top: 75px;
  left: 14px;
  font-size: 10px;
  color: #5F6783;
}

.switchButton:hover {
  color: white;
  cursor: pointer;
}

.inputs {
  position: relative;
}

.assetOne {
  position: absolute;
  height: 30px;
  top: 36px;
  right: 20px;
  border-radius: 100px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  font-weight: bold;
  font-size: 17px;
  padding-right: 8px;
}

.assetTwo {
  position: absolute;
  height: 30px;
  top: 135px;
  right: 20px;
  border-radius: 100px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  font-weight: bold;
  font-size: 17px;
  padding-right: 8px;
}

.assetOne:hover{
  cursor: pointer;
}

.assetTwo:hover{
  cursor: pointer;
}

.assetOne .logo,
.assetTwo .logo{
  padding-right: 0;
}

.assetLogo {
  height: 22px;
  margin-left: 5px;
}

.swapButton {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #283660;
  width: calc(100%);
  height: 55px;
  font-size: 20px;
  border-radius: 12px;
  color: #E8FFCE;
  font-weight: bold;
  transition: 0.3s;
  margin-bottom: 30px;
  margin-top: 8px;
}

.swapButton[disabled] {
  background-color: #243056;
  opacity: 0.4;
  color: rgba(232, 255, 206, 0.5);
}

.swapButton[disabled]:hover {
  cursor: not-allowed;
  background-color: #243056;
}

.swapButton:hover {
  cursor: pointer;
  background-color: #3b4874
}

.tokenLogo {
  height: 40px;
  width: 40px;
}

.modalContent {
  border-top: 1px solid #363e54;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.tokenChoice {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 7px 15px;
  margin: 0 10px;
  border-radius: 5px;
}

.tokenChoice:hover {
  cursor: pointer;
  background-color: #1f2639;
  color: #E8FFCE;
}

.tokenChoice:hover .tokenTicker {
  color: #E8FFCE;
}

.tokenName {
  margin-left: 10px;
  font-size: 16px;
  font-weight: 500;
}

.tokenTicker {
  margin-left: 10px;
  font-size: 13px;
  font-weight: 300;
  color: #51596f;
}

.pulsating-img-container {
  position: relative;
  display: inline-block;
}

@keyframes pulsate {
  0% {
    transform: scale(0.1, 0.1);
    opacity: 0;
  }
  
  50% {
    opacity: 1;
  }
  
  100% {
    transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

.ratesLogoWrapper {
  width: 100%;
  text-align: left;
  margin: 5px 0;
}

.ratesLogoInner{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ratesLogo {
  padding: 2px;
  display: block;
}

.ratesLogoIcon {
  width: 18px;
  height: 18px;
  margin-right: 5px;
  display: inline-block;
  vertical-align: top;
}

.ratesLogoToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #283660;
  font-size: 12px;
  border-radius: 7px;
  color: #E8FFCE;
  font-weight: bold;
  transition: 0.3s;
  border: 0;
  padding: 6px 12px;
}

.ratesLogoToggle[disabled] {
  background-color: #243056;
  opacity: 0.4;
  color: rgba(232, 255, 206, 0.5);
}

.ratesLogoToggle:hover {
  cursor: pointer;
  background-color: #3b4874
}

.approx {
  position: relative;
}

.approx::before {
  content: '≈';
  position: absolute;
  z-index: 2;
  font-size: 26px;
  top: 50%;
  margin-top: -18px;
  left: 14px;
}